import React from 'react';
import { Container, Card, CardHeader, CardBody } from 'reactstrap';
import { useDispatch, useSelector } from 'react-redux';
import LockTable from './components/LockTable';
import AddLockForm from './components/AddLockForm';
import { useEffect } from 'react';
import { getVenues } from '../../../redux/actions/venuesActions';

function EtheraAccess() {
    const dispatch = useDispatch();
    const venues = useSelector(state => state.venues.data.result);
    const locks = useSelector(state => state.venues.locks);

    useEffect(() => {
        dispatch(getVenues());
    }, []);

    return (
        <Container className="ethera-access">
            <Card>
                <CardHeader>
                    <h2>Ethera Access</h2>
                </CardHeader>
                <CardBody>
                    <AddLockForm venues={venues}/>
                    <LockTable data={locks} venues={venues}/>
                </CardBody>
            </Card>
        </Container>
    );
}

export default EtheraAccess;