import React from 'react';

function EditableTableCell(props) {
    const {isEditing, value, defaultValue} = props;
    const textValue = defaultValue !== undefined ? defaultValue : value;
    const lengthCuttingValue = 50;
    return (
        <td>
            {!isEditing && <span title={textValue.length > lengthCuttingValue ? textValue : ""}>{textValue.length > lengthCuttingValue ? textValue.slice(0, lengthCuttingValue) + "..." : textValue}</span>}
            {isEditing && <EditableTableCellInput {...props}/>}
        </td>
    );
}

function EditableTableCellInput(props) {
    const {onChange, element, value, type} = props;
    switch(type) {
        case "text":
            return <input value={value} onChange={(e) => onChange && onChange(e.target.value)}/>;
        case "element":
            return element;
        default:
            return null;
    }
}

export default EditableTableCell;