import React from 'react';
import { Col, Label, Input, Button, Form } from 'reactstrap';
import './AddLockForm.scss';
import { useState } from 'react';
import VenuesDropdown from './VenuesDropdown';
import { useDispatch } from 'react-redux';
import { testLock, addLock } from '../../../../redux/actions/locksActions';

function AddLockForm() {
    const [venue, setVenue] = useState("");
    const [name, setName] = useState("");
    const [token, setToken] = useState("");
    const dispatch = useDispatch();

    const test = () => {
        if (venue && token) {
            dispatch(testLock(venue, token));
        }
    }

    const add = () => {
        if (name && venue && token) {
            dispatch(addLock({ name, venue, token }));
        }
    }

    return (
        <Form>
            <fieldset className="row add-lock-form">
                <legend>Add new Lock</legend>
                <Col sm={2}>
                    <Label htmlFor="lock-name">Name</Label>
                    <Input id="lock-name" value={name} onChange={(e) => setName(e.target.value)} />
                </Col>
                <Col sm={3}>
                    <Label htmlFor="add-lock-venue">Venue</Label>
                    <VenuesDropdown id="add-lock-venue" value={venue} onChange={setVenue} defaultFirst={true} />
                </Col>
                <Col sm={4}>
                    <Label htmlFor="lock-token">Cloud Token</Label>
                    <Input id="lock-token" value={token} onChange={(e) => setToken(e.target.value)} />
                </Col>
                <Col sm={3}>
                    <div>&nbsp;</div>
                    <Button onClick={test} color="primary" disabled={!venue || !token}>Test lock</Button>
                    <Button onClick={add} color="success" disabled={!name || !venue || !token}>Add</Button>
                </Col>
            </fieldset>
        </Form>
    );
}

export default AddLockForm;