import React from 'react';
import { Row, Col, Table } from 'reactstrap';
import VenuesDropdown from './VenuesDropdown';
import { useState } from 'react';
import { useEffect } from 'react';
import './LockTable.scss';
import { useDispatch, useSelector } from 'react-redux';
import { getLocks, deleteLock } from '../../../../redux/actions/locksActions';
import LockRow from './LockRow';
import ConfirmDeleteModal from '../../../../components/Modal/ConfirmDeleteModal.jsx';

function LockTable() {
    const [venue, setVenue] = useState("");
    const dispatch = useDispatch();
    const locks = useSelector(state => state.locks.data.result);
    const venues = useSelector(state => state.venues.data.result);
    const mappedVenues = {};
    if (venues) {
        for (let venue of venues) {
            mappedVenues[venue.id] = venue.name;
        }
    }

    useEffect(() => {
        if (venue) {
            dispatch(getLocks(venue));
        }
    }, [venue]);

    const [editingLock, setEditingLock] = useState(undefined);
    const setEditLock = lock => setEditingLock(lock.id);

    const [deleteModalOpen, setModalDeleteOpen] = useState(false);
    const [deletingLock, setDeletingLock] = useState({});
    const toggleDelete = () => setModalDeleteOpen(!deleteModalOpen);
    const openDeleteModal = (lock) => {
        setDeletingLock(lock);
        setModalDeleteOpen(true);
    }
    const requestDeleteLock = () => {
        toggleDelete();
        dispatch(deleteLock(deletingLock));
    }

    return (
        <Row className="locks-table">
            <Col sm={12}>
                <h5>Current locks</h5>
                <div className="venues">
                    <VenuesDropdown id="venues-table-dropdown" value={venue} onChange={setVenue} defaultFirst={true} />
                    <Table className="align-items-start table-sort locks-table-content" responsive>
                        <thead>
                            <tr>
                                <th className="">Name</th>
                                <th className="">Venue name</th>
                                <th className="">Token</th>
                                <th className="">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                locks && locks.map(lock => <LockRow row={lock} venues={mappedVenues} key={lock.id} isEditing={editingLock === lock.id} onEdit={setEditLock} onDelete={openDeleteModal} />)
                            }
                            {
                                locks && locks.length === 0 && <tr><td colSpan="4">No locks registered</td></tr>
                            }
                        </tbody>
                    </Table>
                </div>
                <ConfirmDeleteModal
                    className="modal-dialog-centered"
                    isOpen={deleteModalOpen}
                    toggle={toggleDelete}
                    handle={requestDeleteLock}
                    content={`Do you want to delete the lock "(${deletingLock.name})"?`}
                />
            </Col>
        </Row>
    );
}

export default LockTable;
