import React from 'react';
import { Button } from 'reactstrap';
import { useDispatch } from 'react-redux';
import { unlock,updateLock } from '../../../../redux/actions/locksActions';
import EditableTableCell from '../../../../components/EditableTableCell/EditableTableCell';
import { useState } from 'react';
import { useEffect } from 'react';
import VenuesDropdown from './VenuesDropdown';


function LockRow(props) {
    const {row, venues, isEditing, onEdit, onDelete} = props;
    const dispatch = useDispatch();

    useEffect(() => {
        if (isEditing) {
            onEdit({});
        }
    }, [row]);

    const sendUnlockCommand = () => {
        dispatch(unlock(row.venue, row.id));
    }

    const [editData, setEditData] = useState({...row});
    const setData = (field, value) => {
        if (field === "id") { //Guard against potential big bug
            return;
        }
        const eData = {...editData};
        eData[field] = value;
        setEditData(eData);
    }

    const saveEdit = () => {
        const editFields = {...editData};
        editFields.currentVenueId = row.venue;
        dispatch(updateLock(editFields));
    }

    const cancelEdit = () => {
        setEditData({...row});
        onEdit({})
    }

    return (
        <tr>
            <EditableTableCell value={editData.name} defaultValue={row.name} type="text" isEditing={isEditing} onChange={(e) => setData("name", e)}/>
            <EditableTableCell defaultValue={venues[row.venue]} type="element"
                element={<VenuesDropdown id="venues-table-dropdown" value={editData.venue} onChange={(e) => setData("venue", e)} defaultFirst={true}/>} 
                isEditing={isEditing} />
            <EditableTableCell value={editData.token} defaultValue={row.token} type="text" isEditing={isEditing} onChange={(e) => setData("token", e)}/>
            <td>
                {
                    isEditing ? 
                    <>
                        <Button size="sm" color="primary" onClick={saveEdit}>Save</Button>
                        <Button size="sm" color="danger" onClick={cancelEdit}>Cancel</Button>
                    </> :
                    <>
                        <Button size="sm" color="warning" onClick={sendUnlockCommand}>Test lock</Button>
                        <Button size="sm" color="success" onClick={() => onEdit(row)}>Edit</Button>
                        <Button size="sm" color="danger" onClick={() => onDelete(row)}>Delete</Button>
                    </>
                }
            </td>
        </tr>
    );
}

export default LockRow;
