import React from 'react';
import { useSelector } from 'react-redux';
import { useEffect } from 'react';

function VenuesDropdown(props) {
    const {value, onChange, id, defaultFirst} = props;

    const venues = useSelector(state => state.venues.data.result);

    useEffect(() => {
        if (defaultFirst && venues && venues.length > 0 && value === "") {
            onChange(venues[0].id);
        }
    }, [venues]);

    return (
        <select id={id} value={value} onChange={(e) => onChange(e.target.value)}>
            {(!venues || venues.length === 0) && <option value="" disabled>-- No venues found --</option>}
            {venues && venues.length > 0 && 
            <>
                {!defaultFirst && <option value="" disabled>-- Select venue --</option>}
                {venues.map(v => <option value={v.id} key={v.id}>{v.name}</option>)}
            </>}
        </select>
    );
}

export default VenuesDropdown;